import { useState } from "react";
import { Row, Col, Button, Modal } from "react-bootstrap";
import { CopyToClipboard } from "react-copy-to-clipboard";

function WalletModal() {
	const [show, setShow] = useState(false);
	const [address, setAddress] = useState({
		value:
			"Nimas Wahyu Ningrum - Jl. Marina emas Barat VI B-170, Keputih, Kec. Sukolilo, Surabaya, Jawa Timur 60111",
		copy_value:
			"Jl. Marina emas Barat VI B-170, Keputih, Kec. Sukolilo, Surabaya, Jawa Timur 60111",
		copied: false,
	});
	const [digitalWallet, setDigitalWallet] = useState({
		value: "BCA 7880856493 a.n Nimas Wahyu Ningrum",
		copy_value: "7880856493",
		copied: false,
	});

	const handleClose = () => setShow(false);
	const handleShow = () => setShow(true);

	return (
		<>
			<Button data-aos="zoom-in" variant="secondary" onClick={handleShow}>
				Open Wallet
			</Button>

			<Modal
				show={show}
				onHide={handleClose}
				aria-labelledby="contained-modal-title-vcenter"
				centered
				size="lg"
			>
				<Modal.Body>
					<span className="name mb-4">Send gift to Nimas and Fatur</span>
					<Row>
						<Col md={6} sm={12} className="mb-3 mb-md-0">
							<span className="description title">Address</span>
							<span className="description text">{address.value}</span>
							<CopyToClipboard
								text={address.copy_value}
								onCopy={() =>
									setAddress({
										value: address.value,
										copy_value: address.copy_value,
										copied: true,
									})
								}
							>
								<Button variant="primary">Copy</Button>
							</CopyToClipboard>
						</Col>
						<Col md={6} sm={12} className="mb-3 mb-md-0">
							<span className="description title">Digital Wallet</span>
							<span className="description text">{digitalWallet.value}</span>
							<CopyToClipboard
								text={digitalWallet.copy_value}
								onCopy={() =>
									setDigitalWallet({
										value: digitalWallet.value,
										copy_value: digitalWallet.copy_value,
										copied: true,
									})
								}
							>
								<Button variant="primary">Copy</Button>
							</CopyToClipboard>
						</Col>
					</Row>
				</Modal.Body>
			</Modal>
		</>
	);
}

export default WalletModal;
